
import type { Block } from '../../core/block.js';
import type { CGenerator } from './c_generator.js';

import { Order } from './c_generator.js';

import { globalNames, globalStructNames, globalintNames, globalStructVarNames } from './c_generator.js';

export function custom_get_temparature(block: Block, generator: CGenerator): string {
  const code = "get_temparature();"; // replace with actual C code
  return code;
}
export function char(block: Block, generator: CGenerator): string {
  var nameValue = block.getFieldValue('NAME');
  var contextValue = block.getFieldValue('context');

  if (!nameValue || !contextValue) {
    return 'NULL';
  }

  // Only add the name to globalNames if it's not already included.
  if (!globalNames.includes(nameValue)) {
    globalNames.push(nameValue);
  }

  var code = 'char ' + nameValue + '[] = "' + contextValue + '";\n';
  return code;
}

export function string_(block: Block, generator: CGenerator): string {
  var nameValue = block.getFieldValue('NAME');

  if (!nameValue) {
    return 'NULL';
  }

  // Only add the name to globalNames if it's not already included.
  if (!globalNames.includes(nameValue)) {
    globalNames.push(nameValue);
  }

  var code = 'char* ' + nameValue + ';\n';
  return code;
}

export function customcharname(block: Block, generator: CGenerator): [string, Order] {
  const name = block.getFieldValue('NAME');
  return [name, Order.ATOMIC];
}

export function customintname(block: Block, generator: CGenerator): [string, Order] {
  const name = block.getFieldValue('NAME');
  return [name, Order.ATOMIC];
}

export function customStructname(block: Block, generator: CGenerator): [string, Order] {
  const name = block.getFieldValue('NAME');
  return [name, Order.ATOMIC];
}

export function alarm_struct(block: Block, generator: CGenerator): [string, Order] {
  var dropdown_name = block.getFieldValue('NAME');
  var text_name = block.getFieldValue('TEXT');

  if (!globalStructVarNames.includes(text_name)) {
    globalStructVarNames.push(text_name);
  }

  var code = 'Struct ' + dropdown_name + ' ' + text_name + ';\n';
  code += generator.statementToCode(block, 'nextStatement');
  return [code, Order.ATOMIC];
}

export function uint32_t(block: Block, generator: CGenerator): string {
  let variableName = block.getFieldValue('NAME');

  if (!variableName) return 'NULL'; // Or handle this case differently

  if (!globalintNames.includes(variableName)) {
    globalintNames.push(variableName);
  }

  var code = 'uint32_t ' + variableName + ';\n';
  code += generator.statementToCode(block, 'nextStatement');
  return code;
}

export function string_set(block: Block, generator: CGenerator): string {
  const variableName = block.getFieldValue('NAME');
  const value = generator.valueToCode(block, 'VALUE', Order.ATOMIC);
  var st_length = value.length + 1;
  let realloc = 'char* ' + variableName + '= realloc(' + variableName +
    ',' + st_length + ' * sizeof(char));\n';
  let memset = 'memset(' + variableName + ', 0 ,' + st_length + ' * sizeof(char));\n';
  let strcpy = 'strcpy(' + variableName + ',' + value + ");\n";

  let code = realloc + memset + strcpy;

  // Generate code for the next statement.
  code += generator.statementToCode(block, 'nextStatement');

  return code;
}

export function int_set(block: Block, generator: CGenerator): string {
  const variableName = block.getFieldValue('NAME');
  const value = generator.valueToCode(block, 'VALUE', Order.ATOMIC);

  // Generate code for the assignment.
  let code = variableName + ' = ' + value + ';\n';

  // Generate code for the next statement.
  code += generator.statementToCode(block, 'nextStatement');

  return code;
}

export function structure(block: Block, generator: CGenerator): string {
  const name = block.getFieldValue('NAME');
  let code = 'typedef struct {\n';

  let branch = generator.statementToCode(block, 'FIELDS');

  if (!globalStructNames.includes(name)) {
    globalStructNames.push(name);
  }
  // Add indentation and trim trailing spaces.
  branch = generator.prefixLines(branch, '  ').replace(/\s+$/, '');

  code += branch;

  code += '\n} ' + name + ';\n';

  return code;
};

export function dependent_dropdown_example(block: Block, generator: CGenerator): [string, Order] {
  const code = '';
  return [code, Order.ATOMIC];
}
